body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background-color: rgba(0, 0, 0, 0.885); */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.home {
  display: flex;
  width: 100vw;
  height: 80vh;
  justify-content: space-around;
  align-items: center;
}

.home > h1 {
  /* color:rgba(55, 55, 55, 0.759); */
  color: white;
  width: 300px;
  height: 200px;
  margin-left: 80px;
  /* background-color: aliceblue; */
  background-color: rgba(55, 55, 55, 0.377);
  /* border:1px solid black; */
  text-align: center;
  bottom: 40px;
  position: relative;
  border-radius: 30px;
}

.home_form {
  width: 500px;
  border: 10px solid;
  border-radius: 50px;
  box-shadow: 1px 1px 4px 0px black;
  padding: 40px;
  color: rgb(154, 156, 156);
  margin-right: 150px;
}

.home_form > input {
  border: 2px solid rgb(147, 147, 147);
  padding-left: 10px;
  outline: none;
  width: 100%;
  height: 20px;
}

.home_form > textarea {
  border: 2px solid rgb(147, 147, 147);
  width: 100%;
  height: 60px;
}

.home_form > button {
  padding: 5px;
  width: 100px;
  color: white;
  background-color: cadetblue;
  font-size: large;
}

.home_form > textarea {
  resize: none;
  padding: 5px;
}
@media screen and (max-width: 768px) {
  .home {
    flex-direction: column;
    height: auto;
    align-items: center;
  }

  .home > h1 {
    width: 80%;
    margin: 20px;
    bottom: initial;
    position: static;
  }

  .home_form {
    width: 80%;
    margin-right: 0;
  }

  .home_form > button {
    width: 100%;
    margin-top: 20px;
  }
}
